.section__card_service{
  padding-bottom: 91px;
}

.service__title_block {
  text-align: center;
  margin-bottom: 2rem;
}

.service__title {
  font-size: 2.5rem;
  font-weight: 700;
  text-transform: uppercase;
}

.service__card_block {
  display: flex;
  justify-content: center;
  align-items: center;
}

.service__card_list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.service__card {
  margin: 1rem;
  padding: 2rem;
  max-width: 25rem;
  border-radius: 1rem;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15);
  background-color: #fff;
  text-align: center;
  transition: transform 0.3s ease-in-out;
  cursor: pointer;
}

.service__card:hover {
  transform: translateY(-0.5rem);
}

.service__card_title {
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 1rem;
}

.service__card_text {
  font-size: 1rem;
  line-height: 1.5;
}
